.alpheios-alignment-editor-align-text-segment .alpheios-alignment-editor-align-text-segment-tokens {
  padding: 10px;
  max-height: 400px;
  overflow-y: scroll;
}

.alpheios-alignment-editor-align-text-segment-row {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 2px;
  border-bottom: 0;
  margin: 0;
  padding: 0;
  font-size: 90%;
  background: #eee;
}
.alpheios-alignment-editor-align-text-segment-row .alpheios-alignment-editor-align-text-segment-row__langname {
  display: inline-block;
  padding: 5px 5px 0 15px;
  color: #000;
  font-weight: bold;
}

span.alpheios-alignment-editor-text-blocks-single__icons {
  display: block;
  padding: 5px;
}
span.alpheios-alignment-editor-text-blocks-single__icons svg {
  display: block;
  width: 25px;
  height: 25px;
  stroke: #99002a;
  fill: transparent;
}

span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data {
  cursor: pointer;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data svg {
  stroke: #99002a;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data {
  cursor: pointer;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data svg {
  stroke: #2a9900;
}