  // padding: 10px;
  // position: relative;
  .alpheios-alignment-editor-align-text-segment-tokens {
    padding: 10px;
    max-height: 400px;
    overflow-y: scroll;
  }
}

.alpheios-align-text-segment-first {
  // padding-top: 40px;
}
  .alpheios-alignment-editor-align-text-segment-row {
      margin: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      // position: absolute;
      min-height: 2px;

      // overflow-y: hidden;
      border-bottom: 0;
      // top: 0;
      // right: 0;
      // left: 0;
      margin: 0;
      padding: 0;
      
      font-size: 90%;

      background: #eee;
      // color: #fff;
      // z-index: 100;

      .alpheios-alignment-editor-align-text-segment-row__langname {
        display: inline-block;
        padding: 5px 5px 0 15px;
        color: #000;
        font-weight: bold;
      }
    }
        span.alpheios-alignment-editor-text-blocks-single__icons {
          display: block;
          padding: 5px;

          svg {
            display: block;
            width: 25px;
            height: 25px;
            stroke: #99002a;
            fill: transparent;
          }
        }

        span.alpheios-alignment-editor-text-blocks-single__icons{

          &.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data {
            cursor: pointer;
            svg {
              stroke: #99002a;
            }
          }

          &.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data {
            cursor: pointer;
            svg {
              stroke: #2a9900;
            }
          }
        }
