.alpheios-alignment-editor-initial-screen__intro {
  background-repeat: repeat-y;
  background-position: 98% 20%;
  background-size: 20% auto;
  overflow: hidden;
  height: 100vh;
}

.alpheios-alignment-editor-initial-screen__heading {
  display: none;
  margin: 20px 60px;
  color: #0E2233;
  font-size: 80px;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
  font-family: "Source Serif Pro", serif;
  text-align: center;
}

.alpheios-alignment-editor-initial-screen__video {
  text-align: center;
}
.alpheios-alignment-editor-initial-screen__video iframe {
  margin: 0;
  border: 0;
  width: 800px;
  height: 500px;
}

.alpheios-header-logo {
  text-align: center;
  margin-top: 80px;
  height: 60px;
}
.alpheios-header-logo .alpheios-logo {
  display: inline-block;
  width: 250px;
}

.alpheios-alignment-editor-initial-screen__buttons {
  width: 700px;
  margin: 0 auto 50px;
  text-align: center;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button {
  width: 49%;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-actions-menu-main-button {
  font-weight: normal;
  font-size: 22px;
  min-width: 315px;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block {
  text-align: center;
  margin-top: 10px;
  border-color: #6e7a84;
  border-width: 1px;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block .alpheios-main-menu-upload-block_item {
  padding: 0;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block .alpheios-actions-menu-button-upload {
  background: #eee;
  color: #46788d;
  font-weight: normal;
}

.alpheios-alignment-editor-initial-screen__heading-animated {
  min-height: 120px;
  margin: 20px 60px;
  color: #0E2233;
  font-size: 80px;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
  font-family: "Source Serif Pro", serif;
  text-align: center;
}
.alpheios-alignment-editor-initial-screen__heading-animated span {
  display: inline-block;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(2) {
  animation-delay: 0.55s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(3) {
  animation-delay: 0.6s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(4) {
  animation-delay: 0.65s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(5) {
  animation-delay: 0.7s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(6) {
  animation-delay: 0.75s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(7) {
  animation-delay: 0.8s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(8) {
  animation-delay: 0.85s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(9) {
  animation-delay: 0.9s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(10) {
  animation-delay: 0.95s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(11) {
  animation-delay: 1s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(12) {
  animation-delay: 1.05s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(13) {
  animation-delay: 1.1s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(14) {
  animation-delay: 1.15s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(15) {
  animation-delay: 1.2s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(16) {
  animation-delay: 1.25s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(17) {
  animation-delay: 1.3s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(18) {
  animation-delay: 1.35s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(19) {
  animation-delay: 1.4s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span:nth-of-type(20) {
  animation-delay: 1.45s;
}
.alpheios-alignment-editor-initial-screen__heading-animated span {
  opacity: 0;
  transform: translate(-150px, -50px) rotate(-180deg) scale(3);
  animation: revolveScale 0.8s forwards;
  animation-delay: 0.5s;
}

@keyframes revolveScale {
  60% {
    transform: translate(20px, 20px) rotate(30deg) scale(0.3);
  }
  100% {
    transform: translate(0) rotate(0) scale(1);
    opacity: 1;
  }
}